export default {
  data () {
    return {
      linkList: [
        { icon: 'iconfenshuxianchaxun-', title: '', url: 'https://www.eol.cn/e_html/gk/chafen/index.shtml?' },
        { icon: 'iconzhiyuanbiao', title: '', url: 'https://www.eol.cn/e_html/gk/zytbsj/index.shtml?province=bj#' },
        { icon: 'iconpingtaizhuanyehua', title: '', url: 'http://m.gaokao.com/beikao/' }
      ]
    }
  },
  methods: {
    goCheck () {
      this.$router.push({ path: '/check' })
    },
    goZhiyuan () {
      this.$router.push({ path: '/zhiyuan' })
    },
    // goGongzhonghao () {
    //   this.$router.push({ path: '/gong' })
    // },
    gotoOption (val) {
      this.$router.push({name: val})
    }
  }
}



