import { getGaokaoDate } from '@/api/index'
export default {
  data () {
    return {
      count: 0
    }
  },
  computed: {
    infoObj () {
      return this.$store.state.user.infoObj
    }
  },
  beforeMount () {
    getGaokaoDate().then(res => {
      this.count = res.data.data
    })
  }
}



