export default {
  methods: {
    gotoPage (name) {
      if (name === 'school') {
        this.$router.push({
          name: name,
          params: { type: 'all' }
        })
      } else if (name === 'bbs') {
        this.$router.push({ path: '/bbs/hot' })
      } else {
        this.$router.push({ name: name })
      }
    }
  }
}



